const Languages = () => {
  const languages = ALL_LANGUAGES.map((lang, index) => {
    const id = index + 1;
    return {
      id,
      name: lang.name,
      phrase: lang.phrase,
    };
  });

  return (
    <div className="languageWrapper">
      <h1>I wasnt sure how to express myself to you or which language to use, so here it is shared in 100 different languages.</h1>
      <div className="cols">
        {languages.map((language) => (
          <div
            className="col"
            key={language.id}
            onTouchStart={(e) => e.currentTarget.classList.toggle('hover')}
            onClick={(e) => e.currentTarget.classList.toggle('hover')}
          >
            <div className="container">
              {/* FRONT */}
              <div
                className="front"
                style={{
                  backgroundImage: `url(https://unsplash.it/${
                    500 + language.id
                  }/${500 + language.id}/)`,
                }}
              >
                <div className="inner">
                  <p>{language.name}</p>
                </div>
              </div>

              {/* BACK */}
              <div className="back">
                <div className="inner">
                  <p>{language.phrase}</p>
                  <button style={{ marginTop: '10px' }} onClick={() => speakText(language.phrase)}>
                    Speak
                  </button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Languages;
